<template>
  <div class="pack-list">
    <div class="header">Local</div>
    <div class="pack-item" v-for="(pack, index) in packs" :key="index">
      <div class="item-number">
        <span>{{ index + 1 }}</span>
      </div>
      <div class="item-content">
        <div class="pack-image-container" v-if="pack.image">
          <img :src="pack.image" alt="Pack Image" class="pack-image" />
        </div>
        <div class="pack-name-container">
          <span class="pack-name">{{ pack.name }}</span>
        </div>
        <div class="pack-version-container" v-if="pack.version">
          <span class="pack-version">{{ pack.version }}</span>
        </div>
        <div class="description-container" v-if="pack.description">
          <span class="pack-description">{{ pack.description }}</span>
        </div>
      </div>
      <div class="controls">
        <button @click="toggleExpanded(index)">
          <span v-if="!pack.expanded">▼</span>
          <span v-else>▲</span>
        </button>
        <button class="remove-btn" @click="removePack(index)">
          <i class="remove-icon">❎</i>
          <span class="remove-text">Remove</span>
        </button>
      </div>
      <div class="technical-details" v-if="pack.expanded">
        <a href="#" @click.prevent="showTechnicalDetails">Technical details</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      packs: [
        {
          name: 'pack_name',
          description: 'pack_description',
          expanded: true,
        },
        {
          image: '/img/custom_map.png',  // Path to your image
          name: 'CustomMapBE DLC',
          version: '1.19.+',
          expanded: false,

        },
        {
          image: '/img/actions_and_stuff.png', // Path to your image
          name: 'Actions & Stuff (1.0.2)',
          expanded: false,

        },
      ],
    };
  },
  methods: {
    toggleExpanded(index) {
      this.packs[index].expanded = !this.packs[index].expanded
    },
    removePack(index) {
      this.packs.splice(index, 1);
    },
    showTechnicalDetails() {
      alert("This would trigger an action to display technical details");
    },
  },
};
</script>

